* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #eee;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.search-form {
  background-color: #c23bc7;
  justify-content: center;
  padding: 6px;
  display: flex;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  box-shadow: 0 7px 6px rgba(52, 7, 253, .31);
}

.input {
  border-style: none;
  border-radius: 2px 0 0 2px;
  padding: 5px;
}

.input:focus {
  outline: none;
}

.submitButton {
  cursor: pointer;
  min-width: 35px;
  border-style: none;
  border-radius: 0 2px 2px 0;
}

.gallery {
  grid-gap: 10px;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  padding: 30px;
  display: grid;
}

.photo-card {
  background-color: #fff;
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.thumb {
  height: 200px;
  width: 100%;
}

.img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  border-radius: 5px 5px 0 0;
  display: block;
}

.info {
  justify-content: space-between;
  gap: 10px;
  padding: 10px;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  font-size: medium;
  display: flex;
}

.load-more {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  touch-action: manipulation;
  background-image: linear-gradient(45deg, #ff512f 0%, #f09819 51%, #ff512f 100%);
  background-size: 200%;
  border: 0;
  border-radius: 10px;
  margin: 10px;
  padding: 15px 30px;
  font-weight: 700;
  transition: all .5s;
  display: block;
  box-shadow: 0 0 14px 7px rgba(194, 59, 199, .506);
}

.load-more:hover {
  color: #fff;
  background-position: 100%;
  text-decoration: none;
}

.load-more:active {
  transform: scale(.95);
}

.footer {
  justify-content: center;
  align-items: center;
  padding-bottom: 30px;
  display: flex;
}

